<template>
  <div id="app">
    <h1 v-text="name"></h1>
    <input type="text" v-model="newItem" @keyup.13="getVal">
    <ul>
      <li v-for="item in items" v-text="item.label" :class="{finished:item.isFinished}" @click="toggleState(item)" @dblclick="deleteFn(item)"></li>
    </ul>
  </div>
</template>

<script>

export default {
  data(){
    return {
      name:'怪诞咖啡',
      items:[],
      newItem:''
    }
  },
  methods:{
    // toggle state
    toggleState(item){
      item.isFinished=!item.isFinished;
    },
    // add list
    getVal(){
      var obj = {
        label:this.newItem,
        isFinished:false
      }
      this.items.push(obj);
      this.newItem = '';
    },
    deleteFn(item){
      var idx = this.items.indexOf(item);
      this.items.splice(idx,1);
    }
  },
  watch:{
    items:{
      handler(val,oldVla){
        window.localStorage.list = JSON.stringify(this.items);
      },
      deep:true
    }
  },
  mounted(){
    var boo = localStorage.list;
    if(boo&&boo!='undefined'&&boo!='null'){
      this.items = JSON.parse(window.localStorage.list);
    }else{
      this.items = [];
    }
  }
}
</script>

<style>
.finished{
  color:red;
}
input{border:solid 1px red;}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



